.component-button{display:inline-block;align-items:center;border-radius:6px;background-color:#0999ff}.component-button>.text{color:#fff;text-transform:uppercase}.component-button.-is-button{border:0 none transparent;outline:0 none transparent;background-image:none;font-family:inherit}.component-button.-is-button>.text{font-family:inherit}.component-button.-is-link{text-decoration:none}.component-button.-is-alternative{background-color:transparent;border:3px solid #fff}.component-button.-is-alternative>.text{font-weight:700}.component-button.-xl{height:62px;padding-left:12px;padding-right:12px;line-height:62px}.component-button.-xl>.text{line-height:62px;font-size:21pt}.component-button.-xl.-is-alternative>.text{line-height:56px}.component-button.-sm{height:36px;padding-left:6px;padding-right:6px}.component-button.-sm>.text{line-height:36px;font-size:14pt}.component-button.-sm.-is-alternative>.text{line-height:30px}