body.-subscribe-message-visible{overflow:hidden;max-width:100vw;max-height:100vh}.subscribe-message{position:absolute;top:50%;left:50%;transform:translate(-50%,-50%);z-index:1;display:flex;align-items:center;flex-direction:column;justify-content:space-around;width:300px;height:200px;border-radius:20px;background-color:rgba(73,73,78,.8);box-shadow:0 8px 10px -4px rgba(0,0,0,.5)}.subscribe-message:before{position:fixed;display:block;width:100vw;height:100vh;background-color:rgba(0,0,0,.2);content:"";z-index:-1}.subscribe-message>.figure{width:100px;height:80px;margin:0}.subscribe-message>.first-line,.subscribe-message>.second-line{margin:0;color:#fff;text-transform:uppercase}.subscribe-message>.first-line{font-size:16pt;font-weight:400}.subscribe-message>.second-line{font-size:28pt;font-weight:700}