.subscribe-container-form,.subscribe-container>.form{box-sizing:border-box;display:flex;align-items:center;flex-direction:column;width:100%;max-width:768px;padding:0 35px}.subscribe-container-form>.field,.subscribe-container>.form>.field{width:100%}.subscribe-container-form>.field+.field,.subscribe-container>.form>.field+.field{margin-top:60px}.subscribe-container{box-sizing:border-box;display:flex;align-items:center;flex-direction:column;min-height:calc(100vh - 175px);padding-top:30px;padding-bottom:50px;background-color:transparent;background-image:linear-gradient(180deg,#0999ff 0,#0952ff)}.subscribe-container>.logo{margin-left:auto;margin-right:auto;margin-bottom:62px}.subscribe-form-button{margin-top:50px;margin-left:auto;display:flex}.subscribe-form-button>.icon{width:20px;height:20px}